@import url(https://fonts.googleapis.com/css2?family=Amatic+SC:wght@400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}
.shopping-cart {
  background-color: lightpink;
  padding: 2%;
}
a {
  text-decoration: none;
  color: grey;
}
.header{background-image:url("https://unsplash.com/photos/F4w_T20l4-o");text-align:center;padding:2%;margin:0;border-bottom:2px solid grey}.header h1{font-size:72px;color:lightpink;font-family:'Amatic SC', cursive;margin:0.5% 0}.header p{font-size:32px;color:grey;margin-top:0.2%;margin-bottom:6%;font-family:'Amatic SC', cursive}.header .options{display:flex;justify-content:space-between}.header .options .option{text-decoration:none;color:grey;font-size:1.2rem;font-family:'Playfair Display', serif;padding:1%}.header .options .option:hover{color:lightpink;border-bottom:1px dashed grey}.header .options .cart{display:flex;flex-direction:row-reverse}.header .options .cart .cart-link{font-size:36px;display:inline-block}.header .options .cart .cart-link .cart-link:hover{cursor:pointer}

.social-bar{display:flex;justify-content:space-between;margin:0 auto;padding:1%}.social-bar .social-link{color:lightpink}

.footer-styles{border-top:2px solid grey;padding-bottom:2%}.footer-styles .footer-nav{padding:1%;display:flex}.footer-styles .social-content{padding-bottom:1%}.footer-styles .social-content .contact{width:10%;margin:0 auto}.footer-styles .social-content .contact p{margin:0;margin-bottom:2%;font-size:24px;font-family:'Amatic SC', cursive}

.product-card{margin:0 auto;display:flex;justify-content:space-between;padding:2%;color:gray}.product-card .image-container:hover{opacity:0.8}.product-card .image-container img{width:100%}.product-card .content{width:45%;display:flex;flex-direction:column;justify-content:center}.product-card .content p{font-size:1.3rem}.product-card .content #purchase{border:1px solid palevioletred;width:30%;margin:0 auto}

.home-page .welcome-content{padding:5%}.home-page .welcome-content h1{margin:0;margin-bottom:2%;font-size:3.5rem;font-family:'Amatic SC', cursive}.home-page .welcome-content p{width:35%;margin:0 auto;line-height:25px}.home-page .featured-product-wrapper{margin-bottom:2%}.home-page .featured-product-wrapper h2{font-family:'Amatic SC', cursive;margin:0;margin-left:2%;font-size:2rem;text-align:left}.home-page .featured-product-wrapper .featured-products{display:flex;justify-content:space-around}

.about-page{background-color:lightpink;padding:5%}.about-page .about-content{max-width:35%;margin:2% auto;padding:2%;background-color:black}.about-page .about-content #profile{width:100%}.about-page .about-content .profile{background-color:lightpink;border:2px solid;padding:2%;color:black;margin-top:2%}.about-page .about-content .profile h2{font-feature-settings:"c2sc";font-variant:small-caps}.about-page .about-content .contact-bar{background-color:black;color:grey;width:35%;margin:0% auto;padding:2%}


